export default "* {\n  box-sizing: border-box;\n}\n\nbody,\nhtml {\n  height: 100%;\n  min-height: 100%;\n}\n\nbody {\n  margin: 0;\n  background-color: #e7e7e7;\n  font-family: \"Roboto\", sans-serif;\n}\n\n/* Credit Card */\n\n.credit-card {\n  margin: auto 0;\n  border-radius: 6px;\n  background-color: #fff;\n  box-shadow: 1px 2px 3px 0px rgba(0, 0, 0, 0.1);\n}\n\n.form-header {\n  padding: 20px 30px 0;\n}\n\n.form-body {\n  margin: 10px auto 0;\n  padding: 30px 30px 20px;\n}\n\n.form-body ion-card-header {\n  padding: 0 !important;\n  margin-bottom: 15px;\n  font-size: 18px;\n  font-weight: 600;\n}\n\n/* Title */\n\n.title {\n  margin: 0 15px;\n  color: #5e6977;\n  font-size: 18px;\n}\n\n/* Common */\n\n.card-number,\n.cvv-input ion-input,\n.month ion-select,\n.paypal-btn,\n.proceed-btn,\n.year ion-select {\n  height: 42px;\n}\n\n.card-number,\n.month ion-select,\n.year ion-select {\n  font-size: 14px;\n  font-weight: 100;\n  line-height: 14px;\n}\n\n.card-number,\n.cvv-details,\n.cvv-input ion-input,\n.month ion-select,\n.year ion-select {\n  color: #2f2f2f;\n  opacity: 0.7;\n}\n\n/* Card Number */\n\n.card-number {\n  width: 100%;\n  margin-bottom: 20px;\n  padding-left: 20px;\n  border: 2px solid #828383;\n  border-radius: 6px;\n}\n\n/* Date Field */\n\n.month ion-select,\n.year ion-select {\n  -moz-appearance: none;\n  -webkit-appearance: none;\n  margin-bottom: 20px;\n  padding-left: 20px;\n  width: 92%;\n  border: 2px solid #828383;\n  border-radius: 6px;\n}\n\n.date-field {\n  position: relative;\n}\n\n.date-field ion-row {\n  padding: 0 !important;\n}\n\n.date-field ion-row ion-col {\n  padding: 0 !important;\n}\n\n/* Card Verification Field */\n\n.card-verification .cvv-input ion-input {\n  width: 145px;\n  padding-left: 20px;\n  border: 2px solid #828383;\n  border-radius: 6px;\n  background: #fff;\n}\n\n/* Buttons Section */\n\nion-button {\n  display: block;\n  margin-top: 25px;\n  text-transform: capitalize;\n  font-size: 14px;\n  height: 35px;\n}\n\n.cards_img ion-list {\n  column-count: 4;\n}\n\n.cards_img ion-list img {\n  margin: 10 12px;\n  max-width: 70px;\n}\n\n.filter_for {\n  box-shadow: none;\n  position: relative;\n}\n\n.filter_for ion-card-header {\n  font-size: 18px;\n  font-weight: 600;\n}\n\n.filter_for ion-card-content {\n  margin: 0 !important;\n}\n\n.filter_for ion-card-content ion-badge {\n  font-size: 12px;\n  padding: 8px 16px;\n  margin: 4px 3px;\n  border-radius: 10px;\n}\n\n.filter_for ion-card-content ion-badge.light {\n  color: var(--bgmedium);\n  background: #f4f5f8;\n}\n\n.filter_for ion-card-content ion-badge.active {\n  color: #fff;\n  background: var(--primary);\n}\n/*# sourceMappingURL=data:application/json;base64, */";